
@import "static/font/font-ukie/css/font-ukie.css";
body {
  font-family: "PingFang SC", "Microsoft YaHei", "Helvetica Neue", Helvetica, arial, sans-serif, tahoma;
  display: none;
}
a {
  color: #6c7988;
  transition: all 0.5s ease-in-out;
  text-decoration: none;
  outline: none !important;
  cursor: pointer;
}
a:hover,
a:active,
a:visited {
  text-decoration: none;
  outline: none !important;
}
a:hover {
  color: #a3c5c3;
}
a:hover h3 {
  color: #a3c5c3;
  transition: all 0.5s ease-in-out;
}
code,
kbd,
pre,
samp {
  font-family: "PingFang SC", "Microsoft YaHei", "Helvetica Neue", Helvetica, arial, sans-serif, tahoma;
}
h1,
h2,
h3,
h4 {
  color: #6c7988;
}
h1 {
  font-size: 40px;
  line-height: 45px;
}
h2 {
  font-size: 30px;
  line-height: 30px;
}
h3 {
  font-size: 24px;
  line-height: 32px;
}
h4 {
  font-size: 16px;
  line-height: 26px;
}
input {
  border-radius: 0;
  box-shadow: none !important;
  -webkit-appearance: none;
}
textarea {
  box-shadow: none !important;
  -webkit-appearance: none;
}
.input-text {
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  margin-bottom: 15px;
  color: #6c7988;
  height: 30px;
  border: none;
  border-bottom: 1px solid rgba(110,110,110,0.1);
  background-color: transparent;
  outline: none;
  transition: all 0.5s ease-in-out;
}
.input-text:hover,
.input-text:focus,
.input-text:active {
  border: none;
  border-bottom: 1px solid #a3c5c3;
  outline: none;
}
.input-text::-webkit-input-placeholder {
  color: #6c7988;
}
.input-text:-ms-input-placeholder {
  color: #6c7988;
}
.input-text:-moz-placeholder {
  color: #6c7988;
}
.input-text::-moz-placeholder {
  color: #6c7988;
}
.input-textarea {
  width: 100%;
  color: #6c7988;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin-bottom: 15px;
  border: none;
  border-bottom: 1px solid rgba(110,110,110,0.1);
  background-color: transparent;
  resize: none;
  outline: none;
  height: 80px;
  transition: all 0.5s ease-in-out;
}
.input-textarea:hover,
.input-textarea:focus,
.input-textarea:active {
  border: none;
  border-bottom: 1px solid #a3c5c3;
  outline: none;
}
.input-textarea::-webkit-input-placeholder {
  color: #6c7988;
}
.input-textarea:-ms-input-placeholder {
  color: #6c7988;
}
.input-textarea:-moz-placeholder {
  color: #6c7988;
}
.input-textarea::-moz-placeholder {
  color: #6c7988;
}
p {
  font-size: 14px;
  font-weight: 300;
  line-height: 28px;
  color: #6c7988;
}
form {
  box-shadow: none;
}
ul {
  padding: 0;
}
ul li {
  list-style-type: none;
}
.img-info {
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: right;
}
.custom-list li {
  padding: 0 0 10px 35px;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px;
}
.custom-list li::before {
  content: "";
  display: inline-block;
  position: relative;
  top: -3px;
  left: -20px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #6c7988;
}
.padding-zero {
  margin-right: -15px;
  margin-left: -15px;
}
@media (max-width: 749px) {
.padding-zero .padding-zero {
    margin-right: 0;
    margin-left: 0;
}
}
.inline-div {
  display: inline-block;
  float: none;
}
.clear {
  clear: both;
}
.text-big {
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
}
.active {
  transition: all 0.5s ease-in-out;
}
.btn-site {
  display: inline-block;
  width: 170px;
  font-size: 14px;
  font-weight: 700;
  line-height: 45px;
  padding: 0;
  margin-bottom: 15px;
  text-align: center;
  border-radius: 50px;
  background-color: #6c7988;
  color: #fff;
  transition: all 0.5s ease-in-out;
}
.btn-site:hover {
  background-color: #a3c5c3;
  color: #fff;
}
@media (max-width: 749px) {
.btn-site {
    width: 100%;
}
}
.btn-submit {
