#jpreOverlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 999999999;
  background-color: #fff;
  text-align: center;
  background-image: url("static/img/loading_pencil.gif");
  background-repeat: no-repeat;
  background-position: center;
}
