.popup-photo {
  visibility: visible;
  opacity: 1;
  z-index: 2;
  position: absolute;
  top: -45px;
  left: 220px;
  transition: all 0.5s ease-in-out;
}
.popup-photo .btn-close {
  position: absolute;
  top: 5px;
  right: 4px;
  font-size: 14px;
  color: #c0c0c0;
  transition: all 0.5s ease-in-out;
}
.popup-photo img {
  width: 100%;
  height: 136px;
  display: block;
  margin: auto;
}
.popup-photo .popup-text {
  color: #6c7988;
  font-size: 14px;
  font-weight: 700;
  line-height: 45px;
  text-align: center;
  height: 65px;
  margin-bottom: 0;
  display: inherit;
}
.popup-photo .popup-place {
  width: 176px;
  height: 199px;
  background-color: #fff;
  visibility: visible;
  opacity: 1;
  z-index: 2;
  position: absolute;
  top: -8px;
  right: 0;
  padding: 20px 20px 35px 20px;
}
.popup-photo .popup-place:after,
.popup-photo .popup-place:before {
  right: 100%;
  top: 32%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  visibility: visible;
  opacity: 1;
}
.popup-photo:before {
  border-width: 9px;
  margin-top: -9px;
}
.popup-photo:after {
  border-color: rgba(255,255,255,0);
  border-right-color: #fff;
  border-width: 8px;
  margin-top: -8px;
}
.popup-photo .shadow-popup {
  display: inline-block !important;
  position: absolute;
  top: 0;
  width: 176px;
  right: 7px;
  padding: 20px 20px 178px 20px;
  background-color: #000;
  visibility: visible !important;
  opacity: 0.1 !important;
  z-index: 1;
  transition: all 0.5s ease-in-out;
}
.popup-photo .shadow-popup:after,
.popup-photo .shadow-popup:before {
  right: 100%;
  top: 32%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.popup-photo .shadow-popup:before {
  border-width: 6px;
  margin-top: -9px;
}
.popup-photo .shadow-popup:after {
  visibility: visible;
  border-color: rgba(255,255,255,0);
  border-right-color: #000 !important;
  border-width: 8px;
  margin-top: -8px;
}
.open-popup {
  display: inline-block;
}
