#menu ul li:first-child a.scroll-to-top {
  position: fixed;
  right: 10px;
  bottom: -48px;
  width: 48px;
  height: 48px;
  padding: 0;
  font-size: 14px;
  line-height: 52px;
  text-align: center;
  z-index: 999;
  color: #fff;
  border-radius: 50%;
  background-color: #d4dbdf;
