
pre {
  padding: 0 10%;
  margin: 0;
  border-radius: 0;
  white-space: pre-wrap;
  border: none;
  background-color: #333;
  color: #d3d3d3;
  font-size: 16px;
}
@media (max-width: 759px) {
pre {
    padding: 0 10px;
}
}
pre .key {
  color: #f0e68c;
}
pre .string {
  color: #0ff;
}
pre .number {
  color: #90ee90;
}
pre .boolean {
  color: #00f;
}
pre .group {
  border-radius: 4px;
}
pre .group:hover {
  background-color: #3f3f3f;
}
pre a {
  position: relative;
  color: #aaa;
  font-weight: 600;
}
pre a:hover {
  text-decoration: underline;
}
pre a .glyphicon {
  position: absolute;
  margin-left: -18px;
}


